import * as React from "react";
import { Button, Modal, Toast } from 'antd-mobile';
import qs from 'qs'


import CourseTitle from '../../components/courseTitle'

import { getOrderSure, onlineSignUp } from '../../api/apply'
import './pay.less';



interface ISprops {
    [propsName: string]: any
}
interface IState {
    modal1: boolean,
    courseTitleData: any,
    payDetail: any,
    staff_list: any,
}
export default class PayDetail extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            modal1: false,
            courseTitleData: {},
            payDetail: {},
            staff_list: [],
        }
    }
    params:any = {};
    pressFlag: boolean = true;
    showModal = (key: any) => (e: any) => {
        e.preventDefault(); // 修复 Android 上点击穿透
        this.setState({
            modal1: true
        })
    }
    onClose = () => {
        this.setState({
            modal1: false,
        });
    }
    onPress = () => {
        this.pressFlag = false;
        this.onClose()
        onlineSignUp(this.params).then( (data) => {
            this.pressFlag = true;
            if(!data.status) {
                Toast.info('报名成功')
                this.props.history.push('/paySuccess')
            }else {
                Toast.fail(data["message"],2)
            }
        })
    }
    getOrderData = () => {
        Toast.loading('')
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        this.params = {
            card_id: data.card_id,
            course_id: data.courseId,
            member_num: data.member_num,
            staff_ids: data.staff_ids
        }
        getOrderSure(this.params).then((data) => {
            Toast.hide()
            if(!data.status) {
                let res = data.data;
                let obj = {
                    course_name: res.course_name,
                    start_time: res.start_time,
                    end_time: res.end_time,
                    pic: res.pic
                }
                this.setState({
                    courseTitleData: obj,
                    payDetail: data.data,
                    staff_list: data.data.staff_list                    
                })


            }
        })
    }
    componentDidMount() {
        document.title = '消费详情';
        this.getOrderData()
    }
    render() {
        const { courseTitleData, payDetail, staff_list } = this.state;
        const { pressFlag } = this;
        return (
            <div className="pay-detail" style={{paddingBottom:'1rem'}}>
                <CourseTitle course={courseTitleData}/>

                <div className="detail-info">
                    <h3>订单信息</h3>
                    <p>会员卡：{payDetail.card_no}</p>
                    <p>{payDetail.cost_type === 1 ? '支付金额：' : '使用次数：'}{payDetail.use_num}{payDetail.cost_type === 1 ? '元' : '次'}</p>
                </div>

                <div className="detail-c">
                    <h3>学员信息</h3>
                    {
                        staff_list.map( (el,index) => {
                            return (
                                <div className="detail-c-i" key={index}>
                                    <p className="tit">{el.nickname}{el.position}</p>
                                    <p className="tips">{el.company}</p>
                                    <p className="tips">{el.cellphone}</p>
                                    <p className="tips">{el.email}</p>
                                </div>
                            )
                        })
                    }
                </div>

                <div className="detail-up">
                    <Button type="primary" className="detail-up-btn" onClick={this.showModal('modal1')}>前往报名</Button>
                </div>

                <Modal
                    visible={this.state.modal1}
                    transparent
                    maskClosable={true}
                    onClose={this.onClose}
                    title="会员服务协议"
                    closable
                    footer={[{ text: '知道了', onPress: pressFlag ? this.onPress : () => {} }]}
                >
                    <div style={{ height: 250, overflow: 'scroll' }}>
                        <p>本网站由中国电信股份有限公司电子渠道运营中心（简称“中国电信”或“我们”）所有和运营。在登录、使用本网站之前，您应当仔细阅读、充分理解《用户服务协议》（简称“本协议”）的全部内容，特别是本协议中以加粗字体显示的内容，您应当重点阅读。如果您不同意本协议或其中任何条款，请您立即停止登录或使用本网站；您开始登录或使用本网站，即表明您已同意本协议内容，本协议即构成对用户和中国电信（合称“双方”）有约束力的法律文件。用户应当按照本协议约定订购商品。用户订购商品时，应当仔细确认所购商品的名称、价格、数量、型号、联系地址、电话、收货人等信息。收货人与用户本人不一致的，收货人/用户的行为和意思表示视为用户/收货人的行为和意思表示，用户/收货人应对收货人/用户的行为及意思表示的法律后果承担连带责任。</p>
                    </div>
                </Modal>



            </div>
        )
    }

}
