import * as React from "react";
import { Toast } from 'antd-mobile'

import { getPayActiveTime } from '../util'
import { getCourse, getafterReport, getEvaluate, getCertificate } from '../api/consumer'
import {withRouter} from "react-router";
import '../assets/less/coursetit.less'
interface ISprops {
    [propName: string]: any,

}
interface IState {
    [statesName: string]: any
}

class CourseTitle extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
        }
    }
    componentDidMount() {
    }
    getCourseInfo = (courseId,memberId) => {
        //判断是否能进入课程信息
        let params = {
            course_id: courseId,
            member_id: memberId
        }
        getCourse(params).then((res) => {
            if (!res.status) {
                this.props.history.push(`/consumer/consumerInfo?course_id=${courseId}&member_id=${memberId}`)
            } else {
                Toast.fail(res["message"],2)
            }
        })
    }
    getAfterCourse = (courseId,memberId) => {
        //判断是否能进入课后报告
        let params = { 
            courseId,
            memberId
        }
        getafterReport(params).then(res => {
            if(!res.status) {
                window.location.href = res.data;
            }else {
                Toast.fail(res["message"],2)
            }
        })
    }
    getEvaluateRule = (courseId,memberId) => {
        //判断是否能进入评价
        let params = {
            course_id: courseId,
            member_id: memberId
        }
        getEvaluate(params).then(res => {
            if (!res.status) {
                this.props.history.push(`/consumer/evaluate?course_id=${courseId}&member_id=${memberId}`)
            } else {
                Toast.fail(res["message"],2)
            }
        })
    }
    getCertificateRule = (courseId,memberId) => {
        //判断是否能进入证书
        let params = {
            courseId,
            memberId
        }
        getCertificate(params).then((res) => {
            if (!res.status) {
                this.props.history.push(`/consumer/certificate?course_id=${courseId}&member_id=${memberId}`)
            } else {
                Toast.fail(res["message"],2)
            }
        })
    }
    render() {
        const { hasTags, course, stuType } = this.props;
        const courseId = this.props.courseId || ''
        const memberId = this.props.memberId || ''
        const time = getPayActiveTime(course.start_time,course.end_time);
        console.log(this.state)
        return (
            <div className="course-title">
                <div className="title-con clearfix" onClick={ this.props.click ? this.props.click : () => {}}>
                    <div className="course-title-l left">
                        <h4>{course.course_name}</h4>
                        <p>上课时间：{time}</p>
                    </div>
                    <div className="course-title-r left">
                        <img src={course.pic} alt="" />
                    </div>
                </div>
                {
                    stuType === 1 || stuType === 3 ? 
                    <div className="title-tips clearfix">
                        <span onClick={() => this.getCourseInfo(courseId,memberId)}>上课信息</span>
                        <span onClick={() => this.getAfterCourse(courseId,memberId)}>课后报告</span>
                        <span onClick={() => this.getEvaluateRule(courseId,memberId)}>评价</span>
                        <span onClick={() => this.getCertificateRule(courseId,memberId)}>领取证书</span>
                    </div>
                    :
                    ''
                }
            </div>

        )
    }

}
//@ts-ignore
export default withRouter(CourseTitle)
